* {
    background: black;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 20px;
}

.App {
    height: 100vh;
}

.nav {
    width: 100%;
    max-height: 128px;
    height: 20vw;
    display: flex;
    justify-content: space-between;
}

.logo {
    font-size: 80px;
    transition: ease-out 120ms;
}

.logo .small {
    font-size: 68px;
}

.logo .big {
    font-size: 100px;
    letter-spacing: 16px;
    font-weight: 600;
}

table {
    width: 100%;
}

table .table-button {
    width: 10vw;
    max-width: 64px;
}

input[type=text] {
    width: 100%;
    background-color: gray;
    padding: 16px 0;
    border: none;
    border-radius: 4px;
}


/* .nav-button {
    width: 128px;
} */

.footer-links {
    bottom: 0;
    width: 100%;
    max-height: 128px;
    height: 20vw;
    display: flex;
    position: absolute;
    justify-content: space-around;
}

@media only screen and (max-width: 600px) {
    .logo {
        font-size: 60px;
    }
    .logo .small {
        font-size: 50px;
    }
    .logo .big {
        font-size: 68px;
        letter-spacing: 16px;
        font-weight: 600;
    }
}